import request from '@/utils/request'
// 查询店铺认证信息
export function getShopAuthInfoByShopId (id) {
  return request({
    url: `/sys/business/shopInfo/getShopAuthInfoByShopId?shopId=${id}`,
    method: 'get',
  })
}
// 认证
export function insertShopAuthInfo(data) {
  return request({
    url: `/sys/business/shopInfo/insertShopAuthInfo`,
    method: 'post',
    data: data
  })
}
// 编辑认证
export function updateShopAuthInfo(data) {
  return request({
    url: `/sys/business/shopInfo/updateShopAuthInfo`,
    method: 'post',
    data: data
  })
}